.settings{
	width: 100%;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.settings__block{
	border: 2px solid #999;
	padding: 10px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-bottom: 15px;
	justify-content: space-between;
}
.settings__logout{
	justify-content: center;
}
.settings__logout button{
	color: #BE4B49;
	border: 1px solid #BE4B49;
	padding: 5px 10px;
	background: none;
	transition: 0.1s ease-in;
}
.settings__logout button:hover{
	background: #BE4B49;
	color: #fff;
}
.settings__cancel{
	border-color: #999 !important;
	color: #999 !important;
}
.settings__cancel:hover{
	background: #999 !important;
	color: #fff !important;
}
.settings__window--bg{
	background: rgba(0,0,0,0.4);
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
}
.settings__window--block{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	display: none;
}
.settings__window--block.active{
	display: flex;
}
.settings__window{
	background: #fff;
	width: 220px;
	height: 100px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	padding: 5px;
	z-index: 3;
}
.setting__buttons{
	display: flex;
	width: 100%;
	justify-content: space-around;
	padding: 0 25px;
}
.settings__setPhoto{
	display: none;
}
.settings__photo--block label:last-child{
	border-top-right-radius: 3px;
	border-bottom-right-radius: 3px;
	background: #2BC48A;
	color: #fff;
	padding: 10px;
	cursor: pointer;
	transition: all 0.1s ease;
}
.settings__photo--block label:last-child:hover{
	background: #1d9165;
}
.settings__photo--block{
	display: flex;
	width: auto;
	align-items: stretch;
}
.settings__photo--block label:first-child{
	padding: 5px 10px 5px 5px;
	display: flex;
	align-items: center;
	border: 1px solid #999;
	border-right: none;
	border-top-left-radius: 3px;
	border-bottom-left-radius: 3px;
	cursor: pointer;
	color: #999;
}
.settings__info{
	display: block;
}
.settings__info--item{
	display: flex;
	width: 100%;
	align-items: center;
	text-align: right;
	margin-bottom: 15px;
}
.settings__info--item input{
	border: 1px solid #999;
	margin-left: 30px;
	padding: 5px;
}
.settings__info--item p{
	width: 30%;
	font-weight: 600;
}
.settings__info--btn{
	display: flex;
	justify-content: center;
}
.settings__info--btn button{
	color: #fff;
  font-size: 16px;
  border: none;
  background: #2BC48A;
  padding: 4px 15px;
  cursor: pointer;
  transition: all 0.1s ease;
}
.settings__info--btn button:hover{
	background: #1d9165;
}